import React, { useState } from 'react'
import MailchimpSubscribe from 'react-mailchimp-subscribe'
import { EmailContainer, EmailInner } from './styles'

const CustomForm = ({ status, message, onValidated }) => {
  const [email, setEmail] = useState('')
  const submit = () => {
    onValidated({ EMAIL: email })
    setEmail('')
  }

  return (
    <EmailContainer>
      <EmailInner>
        {status === 'sending' && <div style={{ color: 'blue' }}>sending...</div>}
        {status === 'error' && (
        <div
          style={{ color: 'red' }}
          dangerouslySetInnerHTML={{ __html: message }}
        />
        )}
        {status === 'success' && (
        <div
          style={{ color: 'green' }}
          dangerouslySetInnerHTML={{ __html: message }}
        />
        )}
        <input
          style={{
            fontFamily: 'inherit', fontSize: '1rm', width: '200px', borderColor: '', outline: 'none', padding: '10px',
          }}
          value={email}
          onChange={e => setEmail(e.target.value)}
          type="email"
          placeholder="Email Address"
        />
        <br />
        <button
          type="button"
          style={{
            fontFamily: 'inherit', border: 'none', outline: 'none', width: '100%', fontSize: '1rm', padding: '10px', color: 'white', background: 'rgb(0, 50, 0)', cursor: 'pointer',
          }}
          onClick={() => submit()}
        >
          Submit
        </button>
      </EmailInner>
    </EmailContainer>

  )
}


const Email = () => {
  const url = 'https://gmail.us19.list-manage.com/subscribe/post?u=31c07122a61d1793401ba033b&id=032c80624d'
  return (
    <div style={{ width: '75vw', padding: '20px', backgroundColor: '' }} id="email">
      <EmailContainer>
        <h1 style={{
          marginTop: '50px',
          marginBottom: '30px',
          color: 'white',
          fontWeight: '300',
          textAlign: 'center',
        }}
        >
                Sign up to join our waitlist!
        </h1>
      </EmailContainer>
      <EmailContainer>
        <EmailInner>
          <MailchimpSubscribe
            url={url}
            render={({ subscribe, status, message }) => (
              <CustomForm
                status={status}
                message={message}
                onValidated={formData => subscribe(formData)}
              />
            )}
          />
        </EmailInner>
      </EmailContainer>
    </div>
  )
}

export default Email
